<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body p-0">
        <div class="row">
          <div class="col-lg-9 col-md-6 col-sm-6 col-6">
            <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
          </div>
          <div class="col-lg-3 col-md-6 col-sm-6 col-6">
            <column-toggle
              v-model="columnDefs"
              @input="cols => this.$refs['table'].updateColumns(cols)"
            ></column-toggle>
          </div>
        </div>
        <div class="col-sm-12 p-0">
          <my-table
            ref="table"
            :columnDefs="columnDefs"
            v-bind:data-rows="products"
            :actions="actions"
            :actionsTable="actionsTable"
            @sortChange="sortChange"
          />
        </div>
      </div>
    </div>
    <el-dialog title="Thêm vào danh mục" :visible.sync="dialogAddCollectionVisible" width="40%" @keyup.enter.native="addCollectionAll">
      <el-checkbox-group v-model="sellectAddCollections">
        <div v-for="collection of collections">
          <el-checkbox :label="collection.id" name="type">{{collection.title}}</el-checkbox>
        </div>
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogAddCollectionVisible = false">Đóng</el-button>
        <el-button type="primary" @click="addCollectionAll">Thêm vào danh mục</el-button>
      </span>
    </el-dialog>
    <el-dialog title="Xóa khỏi danh mục" :visible.sync="dialogRemoveCollectionVisible" width="40%" @keyup.enter.native="removeCollectionAll">
      <el-checkbox-group v-model="sellectRemoveCollections">
        <div v-for="collection of collections">
          <el-checkbox :label="collection.id" name="type">{{collection.title}}</el-checkbox>
        </div>
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogRemoveCollectionVisible = false">Đóng</el-button>
        <el-button type="primary" @click="removeCollectionAll">Xóa khỏi danh mục</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { MessageBox, Message, Button, Dialog, Form, FormItem, Col, CheckboxGroup, Checkbox } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import dtHelper from 'src/helpers/datatable'
  import productSchemas from './product-schemas'
  import ColumnToggle from 'src/components/UIComponents/ColumnToggle'
  import { mapState } from 'vuex'

  export default{
    components: {
      ElButton: Button,
      ElCol: Col,
      ElFormItem: FormItem,
      ElDialog: Dialog,
      ElCheckbox: Checkbox,
      ElForm: Form,
      MyTable,
      MyFilter,
      ColumnToggle
    },
    computed: mapState(['collections', 'products']),
    data () {
      let initFiledArrays = ['id', 'image', 'title', 'price', 'view', 'created_at', 'status'];
      let columnDefs = dtHelper.buildInitFields(productSchemas, initFiledArrays);

      return {
        dialogAddCollectionVisible: false,
        dialogRemoveCollectionVisible: false,
        sellectAddCollections: [],
        sellectRemoveCollections: [],
        currentSelectRow: [],
        filterOutput: [],
        columnDefs: columnDefs,
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          },
          {
            type: 'danger',
            icon: 'nc-icon nc-simple-remove',
            callback: this.remove
          }
        ],
        filter: {},
        rules: dtHelper.buildRules(productSchemas),
        actionsTable: [
          {
            title: 'Ẩn',
            callback: this.inactiveAll
          },
          {
            title: 'Hiện',
            callback: this.activeAll
          },
          {
            title: 'Xóa',
            color: 'text-danger',
            callback: this.removeAll
          },
          {
            title: 'Thêm vào danh mục',
            callback: this.showAddCollectionAll
          },
          {
            title: 'Xóa khỏi danh mục',
            color: 'text-danger',
            callback: this.showRemoveCollectionAll
          }
        ]
      }
    },
    mounted() {
      try {
        this.$store.dispatch('fetchProducts');
        this.$store.dispatch('fetchCollections');
      } catch(e) {}

      this.$store.dispatch('setPageTitle', 'sản phẩm');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Tạo sản phẩm',
        type: 'primary',
        icon: '',
        callback: this.createProduct
      }])
    },
    methods: {
      updateFilter(filterOutput) {
        let filterString = dtHelper.buildQueryString(filterOutput);
        this.$store.dispatch('fetchProducts', {
          filterString: filterString
        });
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchProducts', {
          order: prop + '=' + order
        });
      },
      edit(index, row){
        this.$router.push('/products/' + row.id);
      },
      createProduct(){
        this.$router.push('/products/create');
      },
      remove(index, row){
        MessageBox.confirm('Bạn có chắc chắn xóa sản phẩm không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.$store.dispatch('removeProduct', row.id).then(res => {
            Message({
              message: 'Đã xóa sản phẩm',
              type: 'success'
            });
          }, res => {
            Message({
              message: res.message,
              type: 'error'
            });
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hủy bỏ'
          });
        });
      },
      inactiveAll(rows){
        this.updateStatus(rows, 'inactive');
      },
      activeAll(rows){
        this.updateStatus(rows, 'active');
      },
      removeAll(rows){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.updateStatus(rows, 'delete');
        })
      },
      showAddCollectionAll(rows){
        this.currentSelectRow = rows;
        this.dialogAddCollectionVisible = true;
      },
      showRemoveCollectionAll(rows){
        this.currentSelectRow = rows;
        this.dialogRemoveCollectionVisible = true;
      },
      addCollectionAll(){
        let self = this;
        let arrID = [];
        for (let row of this.currentSelectRow) {
          arrID.push(row.id);
        }
        self.$store.dispatch('addProductToCollection', {
          arrIdCollection: self.sellectAddCollections,
          arrId: arrID
        }).then(function () {
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
          self.dialogAddCollectionVisible = false;
          self.sellectAddCollections = [];
        }).catch(function () {
          Message({
            type: 'error',
            message: 'Có lỗi xảy ra'
          });
        })
      },
      removeCollectionAll(){
        let self = this;
        let arrID = [];
        for (let row of this.currentSelectRow) {
          arrID.push(row.id);
        }
        self.$store.dispatch('removeProductToCollection', {
          arrIdCollection: self.sellectRemoveCollections,
          arrId: arrID
        }).then(function () {
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
          self.dialogRemoveCollectionVisible = false;
          self.sellectRemoveCollections = [];
        }).catch(function () {
          Message({
            type: 'error',
            message: 'Có lỗi xảy ra'
          });
        })
      },
      updateStatus(rows, status){
        let self = this;
        this.$util.updateStatusAll('product', rows, status).then(function (result) {
          self.$store.dispatch('fetchProducts');
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions {
    button.btn {
      margin-right: 5px;
    }
  }
</style>
